* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-family: Roboto, sans-serif;
  color: #1f1f24; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }

.conteiner {
  max-width: 1210px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }

.header__button {
  width: 207px;
  height: 45px;
  border: 1px solid #ff2f96;
  background-color: transparent;
  color: #ff2f96;
  transition: background 0.2s, color 0.2s; }
  .header__button:hover {
    background-color: #ff2f96;
    color: #fff; }

.header__conteiner {
  background-color: #fbfbfb; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 103px; }
  .header__button {
    align-items: center; }
  .header__phone {
    margin-left: auto;
    align-items: center;
    font-size: 25px;
    color: #1f1f24;
    margin-right: 26px;
    transition: color 0.2s; }
    .header__phone:hover {
      color: #ff2f96; }

.menu {
  display: flex; }
  .menu__item {
    margin-right: 32px; }
  .menu__link {
    transition: color 0.2s;
    font-weight: 400;
    color: #1f1f24; }
    .menu__link:hover {
      color: #ff2f96;
      border-bottom: 1px solid #ff2f96; }

.burger {
  display: none;
  background-color: transparent;
  position: relative;
  height: 52px;
  width: 60px;
  border: none;
  cursor: pointer; }
  .burger__btn span {
    left: 6px;
    top: 23px; }
  .burger__btn span, .burger__btn span::before, .burger__btn span::after {
    position: absolute;
    width: 45px;
    height: 6px;
    transition: width 0.4s, transform 0.4s, top 0.4s;
    background: linear-gradient(13deg, rgba(255, 143, 90, 0.9) 26%, rgba(255, 47, 150, 0.95) 72%); }
  .burger__btn span::before, .burger__btn span::after {
    content: '';
    left: 0; }
  .burger__btn span::before {
    top: 12px; }
  .burger__btn span::after {
    top: -12px; }
  .burger__btn_click span {
    width: 0;
    margin-left: 0; }
  .burger__btn_click span::after {
    transform: rotate(45deg);
    margin-top: 0;
    top: 0; }
  .burger__btn_click span::before {
    transform: rotate(-45deg);
    margin-top: 0;
    top: 0; }

.aboutme {
  background-color: #fbfbfb; }
  .aboutme__conteiner {
    padding-top: 9px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; }
  .aboutme__text {
    display: flex;
    grid-column: 1 / 3; }
  .aboutme__photo {
    grid-column: 3 / 4; }
  .aboutme__ellipse {
    background-image: url(../../img/ellipse.svg);
    background-repeat: no-repeat;
    background-position-x: 76px;
    position: absolute;
    width: 100vw; }
  .aboutme__title {
    margin-bottom: 18px;
    font-size: calc(30px + 3vw);
    font-weight: 900;
    color: #1f1f24; }
  .aboutme__subtitle {
    margin-bottom: 45px;
    font-size: calc(12px + 1vw);
    color: #8993ad; }
  .aboutme__text-conteiner {
    margin-top: 105px; }

.photo__img {
  object-fit: fill; }

.button-orange {
  display: block;
  width: 270px;
  height: 60px;
  border: none;
  background-color: #ff4e2e;
  font-size: 18px;
  color: #fff;
  text-align: center;
  line-height: 60px;
  transition: background 0.2s, color 0.2s; }
  .button-orange:hover {
    background-color: #ff2f96; }

@media screen and (min-width: 1440px) {
  .aboutme__title {
    font-size: 80px; }
  .aboutme__subtitle {
    font-size: 25px; } }

@media screen and (max-width: 1024px) {
  .conteiner {
    max-width: 990px; }
  .burger {
    display: block; }
  .header__nav_tablet {
    position: absolute;
    top: 0;
    left: -50vh;
    width: 40vh;
    height: 100vh;
    transition: transform 0.2s linear;
    background-color: rgba(255, 47, 150, 0.97);
    z-index: 1; }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.78);
    transition: transform 0.2s linear;
    z-index: 0; }
  .menu__link_tablet {
    color: #fff;
    font-size: 26px; }
  .header__nav_click {
    transform: translateX(50vh); }
  .menu__tablet {
    flex-direction: column;
    margin-top: 50px; }
  .menu__item_tablet {
    margin: 20px auto; }
  .aboutme__title {
    font-size: calc(30px + 2vw);
    margin-bottom: 25px; }
  .aboutme__text-conteiner {
    margin-top: 150px; }
  .button-orange {
    width: 320px;
    height: 68px;
    line-height: 68px; } }

@media screen and (max-width: 900px) {
  .aboutme__text-conteiner {
    margin-top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw; }
  .aboutme__text {
    grid-column: 1/4;
    grid-row: 2/2;
    justify-self: center;
    margin-top: 0;
    background-color: #fff; }
  .aboutme__photo {
    grid-column: 1/4;
    grid-row: 1/2;
    justify-self: center; } }

@media screen and (max-width: 768px) {
  .header__button {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: 1px solid #ff2f96;
    font-size: 0; }
  .icon-phone__header:before {
    font-size: 20px; }
  .header__button:hover {
    background-color: transparent; }
  .header__phone:hover {
    color: #1f1f24; }
  .menu__link:hover {
    color: #fff; }
  .menu__link_tablet {
    font-size: 20px; }
  .aboutme__subtitle {
    font-size: 16px;
    padding: 0 10px;
    text-align: center; } }

@media screen and (max-width: 425px) {
  .burger {
    height: 35px;
    width: 40px;
    margin-left: -8px; }
    .burger__btn span, .burger__btn span::before, .burger__btn span::after {
      width: 25px;
      height: 3px; }
    .burger__btn span {
      left: 8px;
      top: 17px; }
    .burger__btn span::after {
      top: -8px; }
    .burger__btn span::before {
      top: 8px; }
    .burger__btn_click span::after,
    .burger__btn_click span::before {
      top: 0; }
    .burger__btn_click span {
      width: 0;
      margin-left: 0; }
  .header__phone {
    font-size: 18px;
    margin-left: 0;
    margin-right: 0; }
  .header__button {
    width: 26px;
    height: 26px; }
  .icon-phone__header:before {
    font-size: 14px; }
  .aboutme__conteiner {
    padding-top: 26px; }
  .aboutme__title {
    font-size: 30px;
    margin-top: 12px;
    margin-bottom: 12px; }
  .aboutme__subtitle {
    margin-bottom: 27px;
    line-height: 20px; }
  .button-orange {
    width: 280px;
    height: 55px;
    line-height: 55px;
    font-size: 16px; }
  .photo__img {
    margin-right: -59px; } }
