@media screen and (min-width: 1440px) {
	.aboutme__title {
		font-size: 80px; }

	.aboutme__subtitle {
		font-size: 25px; } }


@media screen and (max-width: 1024px) {
	.conteiner {
		max-width: 990px; }

	.burger {
		display: block; }

	.header__nav_tablet {
		position: absolute;
		top: 0;
		left: -50vh;
		width: 40vh;
		height: 100vh;
		transition: transform $time_menu linear;
		background-color: $color_backing;
		z-index: 1; }

	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0,0,0,.78);
		transition: transform $time_menu linear;
		z-index: 0; }

	.menu__link_tablet {
		color: #fff;
		font-size: 26px; }

	.header__nav_click {
		transform: translateX(50vh); }

	.menu__tablet {
		flex-direction: column;
		margin-top: 50px; }

	.menu__item_tablet {
		margin: 20px auto; }



	.aboutme__title {
		font-size: calc(30px + 2vw);
		margin-bottom: 25px; }

	.aboutme__text-conteiner {
		margin-top: 150px; }

	.button-orange {
		width: 320px;
		height: 68px;
		line-height: 68px; } }





@media screen and (max-width: 900px) {
	.aboutme__text-conteiner {
		margin-top: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100vw; }

	.aboutme__text {
		grid-column: 1/4;
		grid-row: 2/2;
		justify-self: center;
		margin-top: 0;
		background-color: #fff; }


	.aboutme__photo {
		grid-column: 1/4;
		grid-row: 1/2;
		justify-self: center; } }






@media screen and (max-width: 768px) {
	.header__button {
		width: 45px;
		height: 45px;
		border-radius: 50%;
		border: $border_pink;
		font-size: 0; }

	.icon-phone__header:before {
		font-size: 20px; }

	.header__button:hover {
		background-color: transparent; }

	.header__phone:hover {
		color: $color_html; }

	.menu__link:hover {
		color: #fff; }

	.menu__link_tablet {
		font-size: 20px; }





	.aboutme__subtitle {
		font-size: 16px;
		padding: 0 10px;
		text-align: center; } }





@media screen and (max-width: 425px) {

	.burger {
		height: 35px;
		width: 40px;
		margin-left: -8px;

		&__btn span,
		&__btn span::before,
		&__btn span::after {
			width: 25px;
			height: 3px; }

		&__btn span {
			left: 8px;
			top: 17px; }

		&__btn span::after {
			top: -8px; }

		&__btn span::before {
			top: 8px; }

		&__btn_click span::after,
		&__btn_click span::before {
			top: 0; }

		&__btn_click span {
			width: 0;
			margin-left: 0; } }

	.header__phone {
		font-size: 18px;
		margin-left: 0;
		margin-right: 0; }

	.header__button {
		width: 26px;
		height: 26px; }

	.icon-phone__header:before {
		font-size: 14px; }



	.aboutme__conteiner {
		padding-top: 26px; }

	.aboutme__title {
		font-size: 30px;
		margin-top: 12px;
		margin-bottom: 12px; }

	.aboutme__subtitle {
		margin-bottom: 27px;
		line-height: 20px; }

	.button-orange {
		width: 280px;
		height: 55px;
		line-height: 55px;
		font-size: 16px; }

	.photo__img {
		margin-right: -59px; } }







